import React from "react";
import PropTypes from "prop-types";
import {
  Event as Container,
  Image,
  Aside,
  Content,
  Details,
  StyledDate,
  Month,
  Time,
  Tags,
  Tag,
  Footer,
  Button,
  Entrance
} from "./Event.css";

const Event = ({
  image,
  title,
  location,
  tags,
  time,
  entrance,
  registrationLink
}) => {
  const date = new Date(time);
  const entranceDate = new Date(entrance);
  return (
    <Container>
      <Image style={{ backgroundImage: `url(${image})` }} />
      <Content>
        <Details>
          <h3>
            {title}
          </h3>
          <span>
            {location}
          </span>
          <Tags>
            {tags.map(tag =>
              <Tag key={tag}>
                {tag}
              </Tag>
            )}
          </Tags>
        </Details>
        <Aside>
          <StyledDate>
            {date.getDate()}
          </StyledDate>
          <Month>
            {date.toLocaleString("he-IL", { month: "long" })}
          </Month>
          <Time>
            {date.getHours()}:{date.getMinutes()}
          </Time>
        </Aside>
      </Content>
      {entranceDate &&
        <Entrance>
          כניסה מ-<time>
            {entranceDate.getHours()}:{entranceDate.getMinutes()}
          </time>
        </Entrance>}
      <Footer>
        {registrationLink &&
          <a href={registrationLink}>
            <Button type="flat">להרשמה</Button>
          </a>}
      </Footer>
    </Container>
  );
};

Event.propTypes = {
  title: PropTypes.string,
  location: PropTypes.string,
  tags: PropTypes.arrayOf(PropTypes.string).isRequired,
  time: PropTypes.number,
  entrance: PropTypes.number,
  registrationLink: PropTypes.string
};

export default Event;



