import React, { Component } from "react";
import head from "lodash/fp/head";
import matches from "lodash/fp/matches";
import map from "lodash/fp/map";
import update from "lodash/fp/update";
import find from "lodash/fp/find";
import WordPress from "./WordPress";
import Event from "./Event";
import NavItem from "./NavItem";
import { App as Container, Header, Logo, Indicator, Events } from "./App.css";

const wp = new WordPress("https://publishow.000webhostapp.com/wp-json/wp/v2/");

const NAV_ITEMS = [
  {
    title: "הכול",
    filter: {}
  },
  {
    title: "הופעות",
    filter: { tags: ["הופעה"] }
  },
  {
    title: "הרצאות",
    filter: { tags: ["הרצאה"] }
  }
];

class App extends Component {
  state = {
    selectedNavItem: NAV_ITEMS[0],
    events: []
  };

  handleNavItemSelection = navItem => {
    this.setState({ selectedNavItem: navItem });
  };

  async componentDidMount() {
    const tags = await wp.tags.get();
    const posts = await wp.posts.get({ _embed: true });
    this.setState({
      events: posts.map(
        update(
          "tags",
          map(id => {
            const tag = find({ id }, tags);
            return tag.name;
          })
        )
      )
    });
  }

  render() {
    const { events, selectedNavItem } = this.state;
    return (
      <Container style={{ height: window.innerHeight }}>
        <Header>
          <Logo />
          <nav>
            <ul>
              {NAV_ITEMS.map(navItem =>
                <NavItem
                  {...navItem}
                  selected={selectedNavItem.title === navItem.title}
                  onSelect={this.handleNavItemSelection}
                />
              )}
            </ul>
            <Indicator
              index={NAV_ITEMS.findIndex(
                navItem => selectedNavItem.title === navItem.title
              )}
              of={NAV_ITEMS.length}
            />
          </nav>
        </Header>
        <Events>
          {events
            .filter(matches(selectedNavItem.filter))
            .map(event =>
              <Event
                image={head(event._embedded["wp:featuredmedia"]).source_url}
                title={event.title.rendered}
                location={event.acf.location.address}
                tags={event.tags}
                time={Number(event.acf.time)}
                entrance={Number(event.acf.entrance)}
                registrationLink={event.acf.registrationLink}
              />
            )}
        </Events>
      </Container>
    );
  }
}

export default App;



